/* CollegeTable.css */
.college-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .college-table th,
  .college-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .college-table th {
    background-color: #f2f2f2;
  }
  
  .college-table th:hover {
    cursor: pointer;
  }
  
  .search-bar {
    margin-bottom: 20px;
  }
  
  .search-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .infinite-scroll {
    padding: 10px;
    text-align: center;
    font-style: italic;
  }
  